/**
 * Animations Module
 * 
 *     
 */

//  :  prefers-reduced-motion
export function initScrollAnimations() {
  //      
  const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches;
  if (prefersReducedMotion) {
    return; //   ,     
  }
  
  const animatedElements = document.querySelectorAll(
    '.tgg-advantages__item, .tgg-services__item, .tgg-archive__item, .tgg-card'
  );
  
  if (animatedElements.length === 0) return;
  
  const observerOptions = {
    root: null,
    rootMargin: '0px 0px -100px 0px',
    threshold: 0.1
  };
  
  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry, index) => {
      if (entry.isIntersecting) {
        //  requestAnimationFrame  setTimeout   
        requestAnimationFrame(() => {
          setTimeout(() => {
            entry.target.classList.add('tgg-animate-fade-in-up');
            observer.unobserve(entry.target);
          }, index * 100); //    
        });
      }
    });
  }, observerOptions);
  
  animatedElements.forEach(element => {
    observer.observe(element);
  });
}

/**
 * Parallax   hero  (,   /)
 */
export function initParallax() {
  const hero = document.querySelector('.tgg-hero');
  if (!hero) return;
  
  const heroBg = hero.querySelector('.tgg-hero__bg');
  if (!heroBg) return;
  
  //        ( 1023px)
  //     
  //          
  const isMobileOrTablet = () => {
    return window.innerWidth < 1023 || 
           /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
           (navigator.maxTouchPoints && navigator.maxTouchPoints > 2);
  };
  
  //  / ,   
  if (isMobileOrTablet()) {
    return;
  }
  
  //      
  const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches;
  if (prefersReducedMotion) {
    return;
  }
  
  let ticking = false;
  let lastScrollY = 0;
  
  function updateParallax() {
    const scrolled = window.pageYOffset || window.scrollY;
    
    //      
    const rate = scrolled * 0.3;
    
    //  transform     ()
    if (Math.abs(scrolled - lastScrollY) > 0.5) {
      heroBg.style.transform = `translateY(${rate}px)`;
      heroBg.style.willChange = 'transform';
      lastScrollY = scrolled;
    }
    
    ticking = false;
  }
  
  //    
  const handleScroll = () => {
    //       (    )
    if (isMobileOrTablet()) {
      //  ,    
      //        
      heroBg.style.transform = 'translateZ(0)'; //  GPU-,  translateY
      heroBg.style.willChange = 'auto';
      return;
    }
    
    if (!ticking) {
      window.requestAnimationFrame(updateParallax);
      ticking = true;
    }
  };
  
  window.addEventListener('scroll', handleScroll, { passive: true });
  
  //      
  let resizeTimeout;
  window.addEventListener('resize', () => {
    clearTimeout(resizeTimeout);
    resizeTimeout = setTimeout(() => {
      if (isMobileOrTablet()) {
        //        
        heroBg.style.transform = 'translateZ(0)'; //  GPU-,  translateY
        heroBg.style.willChange = 'auto';
      }
    }, 250);
  }, { passive: true });
}


