/**
 * Navigation Module
 * 
 *     
 *   ,  , 
 */

export function initNavigation() {
  const burger = document.querySelector('.tgg-header__burger');
  const nav = document.querySelector('.tgg-header__nav');
  const header = document.querySelector('.tgg-header');
  const navList = document.querySelector('.tgg-nav__list');
  
  if (!burger || !nav) {
    console.warn('Navigation elements not found');
    return;
  }
  
  // ============================================
  // MOBILE MENU TOGGLE
  // ============================================
  
  //     
  let scrollPosition = 0;
  
  //   
  function closeMenu() {
    nav.classList.remove('active');
    burger.classList.remove('active');
    burger.setAttribute('aria-expanded', 'false');
    
    //  
    if (window.innerWidth <= 1023) {
      document.body.classList.remove('menu-open');
      document.documentElement.classList.remove('menu-open');
      document.body.style.top = '';
      window.scrollTo(0, scrollPosition);
    }
  }
  
  burger.addEventListener('click', (e) => {
    e.preventDefault();
    e.stopPropagation();
    
    const isExpanded = burger.getAttribute('aria-expanded') === 'true';
    const willBeExpanded = !isExpanded;
    
    //  aria-expanded
    burger.setAttribute('aria-expanded', willBeExpanded);
    
    //  
    nav.classList.toggle('active');
    burger.classList.toggle('active');
    
    //    
    if (window.innerWidth <= 1023) {
      if (willBeExpanded) {
        //   
        scrollPosition = window.pageYOffset || document.documentElement.scrollTop;
        
        //  
        document.body.classList.add('menu-open');
        document.documentElement.classList.add('menu-open');
        document.body.style.top = `-${scrollPosition}px`;
      } else {
        //  
        document.body.classList.remove('menu-open');
        document.documentElement.classList.remove('menu-open');
        document.body.style.top = '';
        window.scrollTo(0, scrollPosition);
      }
    } else {
      document.body.classList.remove('menu-open');
      document.documentElement.classList.remove('menu-open');
    }
    
    //  aria-label  
    burger.setAttribute('aria-label', willBeExpanded 
      ? ' ' 
      : ' ');
    
    //  
    if (willBeExpanded) {
      const navItems = nav.querySelectorAll('.tgg-nav__list li');
      console.log('Menu opened, found items:', navItems.length);
    }
  });
  
  //      
  document.addEventListener('click', (e) => {
    if (window.innerWidth <= 1023 && nav.classList.contains('active')) {
      if (!nav.contains(e.target) && !burger.contains(e.target)) {
        closeMenu();
      }
    }
  });
  
  //     Escape
  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape' && nav.classList.contains('active')) {
      closeMenu();
    }
  });
  
  //     
  document.addEventListener('wheel', (e) => {
    if (window.innerWidth <= 1023 && nav.classList.contains('active')) {
      e.preventDefault();
      return false;
    }
  }, { passive: false });
  
  //  touchmove (  )
  document.addEventListener('touchmove', (e) => {
    if (window.innerWidth <= 1023 && nav.classList.contains('active')) {
      //     
      if (!nav.contains(e.target)) {
        e.preventDefault();
        return false;
      }
    }
  }, { passive: false });
  
  // ============================================
  // NAVIGATION LINKS
  // ============================================
  
  const navLinks = nav.querySelectorAll('.tgg-nav__link, a');
  
  navLinks.forEach((link) => {
    //   
    link.addEventListener('click', (e) => {
      //   
      link.style.transform = 'scale(0.95)';
      setTimeout(() => {
        link.style.transform = '';
      }, 150);
      
      //      
      if (window.innerWidth <= 1023) {
        setTimeout(() => {
          closeMenu();
        }, 300);
      }
    });
  });
  
  // ============================================
  // ACTIVE MENU ITEM DETECTION
  // ============================================
  
  function setActiveMenuItem() {
    const currentPath = window.location.pathname;
    const currentHref = window.location.href;
    
    //    
    navLinks.forEach((link) => {
      link.classList.remove('active');
    });
    
    //      URL
    navLinks.forEach((link) => {
      const href = link.getAttribute('href');
      if (!href) return;
      
      //  URL  
      const linkUrl = new URL(href, window.location.origin);
      const currentUrl = new URL(currentHref);
      
      //   
      const linkPath = linkUrl.pathname.replace(/\/$/, ''); //  trailing slash
      const currentPathNormalized = currentPath.replace(/\/$/, '');
      
      //   
      if ((linkPath === '' || linkPath === '/') && (currentPathNormalized === '' || currentPathNormalized === '/')) {
        link.classList.add('active');
        return;
      }
      
      //    -   
      if (linkPath !== '' && linkPath !== '/' && currentPathNormalized.includes(linkPath)) {
        link.classList.add('active');
        return;
      }
      
      //    (    )
      if (href.startsWith('#')) {
        const targetId = href.substring(1);
        const targetElement = document.getElementById(targetId);
        if (targetElement) {
          const rect = targetElement.getBoundingClientRect();
          if (rect.top <= 200 && rect.bottom >= 100) {
            link.classList.add('active');
          }
        }
      }
    });
  }
  
  //        URL
  setActiveMenuItem();
  
  //     ( /)
  window.addEventListener('popstate', setActiveMenuItem);
  
  // ============================================
  // HEADER SCROLL EFFECT + ACTIVE MENU DETECTION ()
  // ============================================
  
  let lastScroll = 0;
  let scrollTicking = false;
  let scrollTimeout;
  
  function handleScroll() {
    const currentScroll = window.pageYOffset;
    
    //  header (scrolled )
    if (currentScroll > 50) {
      header?.classList.add('scrolled');
    } else {
      header?.classList.remove('scrolled');
    }
    
    //     (   ,  debounce)
    const anchorLinks = Array.from(navLinks).filter(link => {
      const href = link.getAttribute('href');
      return href && href.startsWith('#');
    });
    
    if (anchorLinks.length > 0) {
      clearTimeout(scrollTimeout);
      scrollTimeout = setTimeout(() => {
        setActiveMenuItem();
      }, 100);
    }
    
    lastScroll = currentScroll;
    scrollTicking = false;
  }
  
  //     requestAnimationFrame
  window.addEventListener('scroll', () => {
    if (!scrollTicking) {
      window.requestAnimationFrame(handleScroll);
      scrollTicking = true;
    }
  }, { passive: true });
  
  // ============================================
  // SMOOTH SCROLL FOR ANCHOR LINKS
  // ============================================
  
  navLinks.forEach(link => {
    const href = link.getAttribute('href');
    
    if (href && href.startsWith('#')) {
      link.addEventListener('click', (e) => {
        const targetId = href.substring(1);
        const targetElement = document.getElementById(targetId);
        
        if (targetElement) {
          e.preventDefault();
          
          navLinks.forEach(l => l.classList.remove('active'));
          link.classList.add('active');
          
          const headerHeight = header?.offsetHeight || 0;
          const targetPosition = targetElement.offsetTop - headerHeight - 20;
          
          window.scrollTo({
            top: targetPosition,
            behavior: 'smooth'
          });
          
          //  URL  
          history.pushState(null, '', href);
        }
      });
    }
  });
  
  // ============================================
  // RESIZE HANDLER
  // ============================================
  
  let resizeTimeout;
  window.addEventListener('resize', () => {
    clearTimeout(resizeTimeout);
    resizeTimeout = setTimeout(() => {
      //       
      if (window.innerWidth > 1023) {
        closeMenu();
      }
    }, 250);
  });
}
