/**
 * Slider Module
 * 
 *    ( Swiper)
 */

export function initSliders() {
  //   Swiper
  if (typeof window.Swiper === 'undefined' || !window.SwiperModules) {
    console.warn('Swiper  .');
    return;
  }
  
  const { Navigation, Pagination, Autoplay } = window.SwiperModules;
  const Swiper = window.Swiper;
  
  //  
  const sliderSelectors = [
    '.tgg-slider-hero',
    '.tgg-slider-gallery',
    '.tgg-slider-tournaments'
  ];
  
  sliderSelectors.forEach(selector => {
    const sliderElement = document.querySelector(selector);
    if (!sliderElement) return;
    
    //    
    if (selector === '.tgg-slider-tournaments') {
      //         
      let currentMaxHeight = 0;
      let isSyncing = false;
      let resizeTimeout = null;
      let lastSyncTime = 0;
      const MIN_SYNC_INTERVAL = 300; //     ()
      
      //           
      function syncSlideHeights(swiper) {
        //      
        if (isSyncing) {
          return;
        }
        
        //    
        const now = Date.now();
        if (now - lastSyncTime < MIN_SYNC_INTERVAL) {
          return;
        }
        
        const slides = swiper.slides;
        if (!slides || slides.length === 0) return;
        
        isSyncing = true;
        lastSyncTime = now;
        
        //  requestAnimationFrame     
        requestAnimationFrame(() => {
          let maxHeight = 0;
          
          //   
          slides.forEach(slide => {
            const card = slide.querySelector('.tgg-tournaments-preview__card');
            if (card) {
              //   
              const oldHeight = card.style.height;
              //       
              card.style.height = '';
              card.style.minHeight = '';
              //   layout
              void card.offsetHeight; // Force reflow
              const height = card.offsetHeight;
              if (height > maxHeight) {
                maxHeight = height;
              }
              //   
              if (oldHeight) {
                card.style.height = oldHeight;
              }
            }
          });
          
          //           
          const heightDifference = Math.abs(maxHeight - currentMaxHeight);
          if (maxHeight > 0 && heightDifference > 5) { //   5px
            currentMaxHeight = maxHeight;
            
            //    requestAnimationFrame   
            requestAnimationFrame(() => {
              slides.forEach(slide => {
                const card = slide.querySelector('.tgg-tournaments-preview__card');
                if (card) {
                  card.style.height = maxHeight + 'px';
                  card.style.minHeight = maxHeight + 'px';
                }
              });
              
              //   Swiper    
              setTimeout(() => {
                if (swiper && swiper.update) {
                  swiper.update();
                }
              }, 50);
            });
          }
          
          isSyncing = false;
        });
      }
      
      // Debounced    resize    
      function debouncedSyncSlideHeights(swiper) {
        if (resizeTimeout) {
          clearTimeout(resizeTimeout);
        }
        resizeTimeout = setTimeout(() => {
          syncSlideHeights(swiper);
        }, 250); //    250
      }
      
      const tournamentsSwiper = new Swiper(sliderElement, {
        modules: [Navigation, Pagination, Autoplay],
        slidesPerView: 1.2,
        spaceBetween: 16,
        loop: false,
        centeredSlides: true, //  
        autoHeight: false, //        
        autoplay: {
          delay: 4000,
          disableOnInteraction: false,
        },
        pagination: {
          el: sliderElement.querySelector('.swiper-pagination'),
          clickable: true,
          dynamicBullets: true,
        },
        navigation: {
          nextEl: sliderElement.querySelector('.swiper-button-next'),
          prevEl: sliderElement.querySelector('.swiper-button-prev'),
        },
        breakpoints: {
          480: {
            slidesPerView: 1.5,
            spaceBetween: 20,
            centeredSlides: true,
            autoHeight: false,
          },
          768: {
            slidesPerView: 2.5,
            spaceBetween: 24,
            centeredSlides: true,
            autoHeight: false,
          },
          1024: {
            slidesPerView: 3.5,
            spaceBetween: 24,
            centeredSlides: true,
            autoHeight: false,
          },
        },
        on: {
          init: function() {
            //        
            setTimeout(() => {
              syncSlideHeights(this);
            }, 200);
          },
          resize: function() {
            //  debounced     
            debouncedSyncSlideHeights(this);
          },
          slideChange: function() {
            //     ,    
          },
          update: function() {
            //     Swiper,   
          },
        },
      });
      
      //      (  )
      const images = sliderElement.querySelectorAll('.tgg-tournaments-preview__card img');
      let imagesLoaded = 0;
      const totalImages = images.length;
      let imagesSyncDone = false;
      
      if (totalImages > 0) {
        const syncAfterImages = () => {
          if (!imagesSyncDone) {
            imagesSyncDone = true;
            setTimeout(() => {
              syncSlideHeights(tournamentsSwiper);
            }, 300);
          }
        };
        
        images.forEach(img => {
          if (img.complete) {
            imagesLoaded++;
            if (imagesLoaded === totalImages) {
              syncAfterImages();
            }
          } else {
            img.addEventListener('load', () => {
              imagesLoaded++;
              if (imagesLoaded === totalImages) {
                syncAfterImages();
              }
            }, { once: true });
            img.addEventListener('error', () => {
              imagesLoaded++;
              if (imagesLoaded === totalImages) {
                syncAfterImages();
              }
            }, { once: true });
          }
        });
      }
    } else {
      //    
      new Swiper(sliderElement, {
        modules: [Navigation, Pagination, Autoplay],
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        autoplay: {
          delay: 5000,
          disableOnInteraction: false,
        },
        pagination: {
          el: sliderElement.querySelector('.swiper-pagination'),
          clickable: true,
        },
        navigation: {
          nextEl: sliderElement.querySelector('.swiper-button-next'),
          prevEl: sliderElement.querySelector('.swiper-button-prev'),
        },
        breakpoints: {
          640: {
            slidesPerView: 2,
          },
          1024: {
            slidesPerView: 3,
          },
        },
      });
    }
  });
}


