/**
 * Forms Module
 * 
 *  , 
 */

export function initForms() {
  const forms = document.querySelectorAll('form[data-tgg-form]');
  
  forms.forEach(form => {
    form.addEventListener('submit', async (e) => {
      e.preventDefault();
      
      const formData = new FormData(form);
      const submitButton = form.querySelector('button[type="submit"]');
      const originalText = submitButton?.textContent;
      
      // 
      if (!validateForm(form)) {
        return;
      }
      
      //  
      if (submitButton) {
        submitButton.disabled = true;
        submitButton.textContent = '...';
      }
      
      try {
        //   AJAX ( )
        const response = await fetch(form.action || window.location.href, {
          method: 'POST',
          body: formData
        });
        
        if (response.ok) {
          showFormMessage(form, '!   .', 'success');
          form.reset();
        } else {
          throw new Error(' ');
        }
      } catch (error) {
        showFormMessage(form, ' .  .', 'error');
      } finally {
        if (submitButton) {
          submitButton.disabled = false;
          submitButton.textContent = originalText;
        }
      }
    });
  });
}

function validateForm(form) {
  const requiredFields = form.querySelectorAll('[required]');
  let isValid = true;
  
  requiredFields.forEach(field => {
    if (!field.value.trim()) {
      isValid = false;
      field.classList.add('error');
      
      field.addEventListener('input', () => {
        field.classList.remove('error');
      }, { once: true });
    }
  });
  
  // Email 
  const emailFields = form.querySelectorAll('input[type="email"]');
  emailFields.forEach(field => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (field.value && !emailRegex.test(field.value)) {
      isValid = false;
      field.classList.add('error');
    }
  });
  
  return isValid;
}

function showFormMessage(form, message, type) {
  const existingMessage = form.querySelector('.tgg-form-message');
  if (existingMessage) {
    existingMessage.remove();
  }
  
  const messageEl = document.createElement('div');
  messageEl.className = `tgg-form-message tgg-form-message--${type}`;
  messageEl.textContent = message;
  
  form.insertBefore(messageEl, form.firstChild);
  
  setTimeout(() => {
    messageEl.remove();
  }, 5000);
}


